.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.add-modal, .edit-modal {
  background-color: #eee;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 250px;
  transition: opacity .25s ease-in;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 5px 5px 8px #000000b3;
}

.add-form, .edit-form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.movies {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 70px;
  list-style: none;
  display: flex;
}

.movies__item {
  border: 2px solid #000;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  display: flex;
}
